<template>
  <div>
    <dynamic-form
      lang="en"
      v-if="DataLoaded"
      :buttons="buttons"
      :schema="FormSchema"
      v-on:OnSubmit="onSubmit"
    ></dynamic-form>
  </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    data(){
        return{
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [                     
                        {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                            config: {
                                options: [],
                                onChange:()=>{}

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProductName'),
                            name: "Product Name",
                            value: "",
                            placeholder: "",
                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('UniqueName'),
                            name: "Unique Name",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange:()=>{}

                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [
                                    {name:'Lost', value :1001},
                                    {name:'Asset Sharing', value :1002},
                                    {name:'Quantity Requested', value :1003},
                                    {name:'Product Accepted', value :1004},
                                    {name:'Return Rejected', value :1005},
                                    {name:'Deploy Product', value :1006},
                                    {name:'Transfer From An Employe To Another Employee', value :1007},
                                    {name:'Location To Location Transfer', value :1008},
                                    {name:'Product Rejected', value :1009},
                                    {name:'Location To Location Transfer Rejected', value :1010},
                                    {name:'Replacement Rejected', value :1011},
                                    {name:'Product was Returned', value :1012},
                                    {name:'Returned Approved', value :1013},
                                    {name:'Location To Location Transfer Approved', value :1014},
                                    {name:'Replacement Request', value :1015},
                                    {name:'Check-Out Request', value :1016},
                                    {name:'Return Request', value :1017},
                                    {name:'Product Assignment', value :1018},
                                    {name:'Replacement Approved', value :1019},],
                                onChange:()=>{}

                            },
                        },
                        
                        {
                            astype: "DateField",
                            label: this.$t('Date'),
                            name: "Date",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "M/D/YYYY" }
                            
                        },
                        {
                            astype: "DateField",
                            label: this.$t('DateEnd'),
                            name: "Date End",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "M/D/YYYY" }
                            
                        },
                        
                        
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search pr-2'></i>Search",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Clear all",

                }
            ],
             DataLoaded:0

        }

        },
        created: async function () {
    var vm = this;
    await vm.GetLocationByCompanyId();
   vm.DataLoaded=1;
  },
        methods:{
            async GetLocationByCompanyId() {
      var vm = this;
      var params = `reqForm=`;
      await DataService.GetLocationByCompanyId(params).then(function (
        response
      ) {
        vm.LocationData = JSON.parse(response.data).data;
        var json = vm.MakeNormalArray(response.data).data;
        $.each(
          json,
          $.proxy(function (index, item) {
            var groupId = item.PARENT_LOCATION_ID;
            var chkGroup = $.grep(vm.LocationData, function (element, index) {
              return element.id == groupId;
            });
            if (chkGroup.length == 0) {
              var returnedData = $.grep(json, function (element, index) {
                return element.PARENT_LOCATION_ID == groupId;
              });
              if (returnedData.length > 0 && returnedData[0].LOCATION_NAME) {
                var objGroup = {
                  id: groupId,
                  label: returnedData[0].LOCATION_NAME,
                  children: returnedData.map((item) => {
                    return {
                      id: item.COMPANY_LOCATION_ID,
                      label: item.LOCATION_NAME,
                    };
                  }),
                };
                vm.FormSchema[0].Data[0].config.options.push(objGroup);
              }
            }
          })
        );
      });
    },
        }
};
</script>