<template>
  <div>
    <dynamic-form
      lang="en"
      v-if="DataLoaded"
      :buttons="buttons"
      :schema="FormSchema"
      v-on:OnSubmit="onSubmit"
    ></dynamic-form>
  </div>
</template>

<script>
import DataService from "../../service/DataService";
export default {
  data() {
    return {
      FormSchema: [
        {
          layoutType: "triple",
          Data: [
            {
              astype: "TreeSelectField",
              label: this.$t("Location"),
              name: "Location",
              value: "",
              placeholder: "",
              validationRules: { required: true, max: 250 },
              config: {
                options: [],
              },
            },
            {
              astype: "SelectField",
              label: this.$t("ProductType"),
              name: "Product Type",
              value: "",
              placeholder: "",

              config: {
                options: [],
                onChange: this.onChangeDropdown,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("ProductName"),
              name: "Product Name",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
            },
            {
              astype: "SelectField",
              label: this.$t("Status"),
              name: "Status",
              value: "",
              placeholder: "",
              config: {
                options: [
                  { name: "Not Returned", value: '0' },
                  { name: "Returned", value: '1' },
                ],
                onChange: () => {},
              },
            },
            {
              astype: "TextField",
              label: this.$t("IssuedTo"),
              name: "Issued To",
              value: "",
              placeholder: "",
            },  
            {
              astype: "TextField",
              label: this.$t("IssuedBy"),
              name: "Issued By",
              value: "",
              placeholder: "",
              config: {},
            },
            {
              astype: "DateField",
              label: this.$t("IssueDate"),
              name: "IssueDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("IssueDateEnd"),
              name: "IssueDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("ReturnDate"),
              name: "ReturnDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("ReturnDateEnd"),
              name: "ReturnDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
          ],
        },
      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-search pr-2'></i>Search",
        },

        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Clear all",
        },
      ],
        DataLoaded:0
    };
  },
  created: async function () {
    var vm = this;
    await vm.GetLocationByCompanyId();
    await vm.BindItemTypeByUserId();
   vm.DataLoaded=1;
  },
  methods: {
    async GetLocationByCompanyId() {
      var vm = this;
      var params = `reqForm=`;
      await DataService.GetLocationByCompanyId(params).then(function (
        response
      ) {
        vm.LocationData = JSON.parse(response.data).data;
        var json = vm.MakeNormalArray(response.data).data;
        $.each(
          json,
          $.proxy(function (index, item) {
            var groupId = item.PARENT_LOCATION_ID;
            var chkGroup = $.grep(vm.LocationData, function (element, index) {
              return element.id == groupId;
            });
            if (chkGroup.length == 0) {
              var returnedData = $.grep(json, function (element, index) {
                return element.PARENT_LOCATION_ID == groupId;
              });
              if (returnedData.length > 0 && returnedData[0].LOCATION_NAME) {
                var objGroup = {
                  id: groupId,
                  label: returnedData[0].LOCATION_NAME,
                  children: returnedData.map((item) => {
                    return {
                      id: item.COMPANY_LOCATION_ID,
                      label: item.LOCATION_NAME,
                    };
                  }),
                };
                vm.FormSchema[0].Data[0].config.options.push(objGroup);
              }
            }
          })
        );
      });
    },

    async BindItemTypeByUserId() {
      let url = `type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=`;
      var vm = this;
      await DataService.AssignGetAssetType(url).then((response) => {
        response.data.forEach((itm, index) => {
          vm.FormSchema[0].Data[1].config.options.push({
            name: itm.ASSETTYPE,
            value: itm.ASSETID,
          });
        });
      });
    },

    async onChangeDropdown(evt) {
      debugger;
      var vm = this;
      var params = `assetTypeid=${evt.target.value}`;
      await DataService.GetAssetCatalogueType(params).then(function (response) {
        response.data.forEach((itm, index) => {
          vm.FormSchema[0].Data[2].config.options.push({
            name: itm.NAME,
            value: itm.ASSETID,
          });
        });
      });
    },
  },
};
</script>